export class Utils{
    /* DISTANCE FUNCTIONS*/
    distSqr(x1,y1, x2,y2) { 
        return this.sqr(x1 - x2) + this.sqr(y1 - y2); 
    }

    dist(x1, y1, x2, y2){
        return Math.sqrt(this.distSqr(x1,y1,x2,y2));
    }

    sqr(x){
        return x * x;
    }
    /* MATH FUNCTIONS */

    clamp(value, min, max){
        return Math.max(Math.min(value,max),min);
    }

    /* STRING FUNCTIONS */

    pad(str, padValue, before, toLength){
        var i;
        var returnStr = str;
        for (i = str.length; i < toLength; i++){
            if (before){
                returnStr = padValue + returnStr;
            }else{
                returnStr = returnStr + padValue;
            }
        }
        return returnStr;
    }

    /* COLOR FUNCTIONS */
    hightlightColor (color) {
        var f = parseInt(color.slice(1),16),
            R = f >> 16,
            G = f >> 8 & 0x00FF,
            B = f & 0x0000FF;
            var hsv = this.RGBtoHSV(R, G, B);

            if (hsv.v < 0.5){
                hsv.v += 0.4;
            }else{
                hsv.v -= 0.4;
            }

            var rgb = this.HSVtoRGB(hsv.h, hsv.s, hsv.v);
            R = rgb.r;
            G = rgb.g;
            B = rgb.b;
            /*
            if (R > 200 || G > 200 || B > 200){
                // Make color darker
                ret = "#" + pad(Math.round((clamp((R * 0.8),0,255) << 16) + (clamp(G * 0.8, 0, 255) << 8) + clamp(B * 0.8,0,255)).toString(16), "0", true, 6) ;
            }else{
                // Make color brighter
                ret = "#" + pad(Math.round((clamp(((R + 20) * 1.2),0,255) << 16) + (clamp(((G + 20) * 1.2),0,255) << 8) + clamp(((B + 20) * 1.2),0,255)).toString(16), "0", true, 6);
            }*/
            var ret = this.rgbToHexString(R,G,B);
            return ret;
        //return "#"+(0x1000000 + (Math.round((t-R)*p)+R) * 0x10000 + (Math.round((t-G)*p)+G) * 0x100 + (Math.round((t-B)*p)+B)).toString(16).slice(1);
    }

    rgbToHexString(R,G,B){
        return "#" + this.pad(Math.round((this.clamp(R,0,255) << 16) + (this.clamp(G,0,255) << 8) + this.clamp(B,0,255)).toString(16), "0", true, 6)
    }

    // returns N different colors, evenly spaced in the color wheel
    getColorWheelSet(n, randomStart){

        var colors = [];
        var h = randomStart? Math.random() : 0;
        var s = 0.63;
        var v = 0.82;
        var separation = 1 / n;
        for (var i = 0; i < n; i++){
            h += separation;
            if (h > 1){
                h -= 1;
            }
            var rgbColor = this.HSVtoRGB(h,s,v);
            colors.push(this.rgbToHexString(rgbColor.r, rgbColor.g, rgbColor.b));
        }

        return colors;
    }

    // returns N different colors, shades of a root color
    getShadesFromColor(n, rootColor){
        var colorRGB = this.hexToRgb(rootColor);
        var color = this.RGBtoHSV(colorRGB.r,colorRGB.g,colorRGB.b);
        var colors = [];
        var h = color.h;
        var s = color.s;
        var v = color.v;
        var separation = s/(2*n);
        for (var i = 0; i < n; i++){
            s -= separation;
            if (s < 0){
                s = 0;
            }
            var rgbColor = this.HSVtoRGB(h,s,v);
            colors.push(this.rgbToHexString(rgbColor.r, rgbColor.g, rgbColor.b));
        }

        return colors;
    }
    hexToRGBA(hex, alpha){
        var c;
        alpha = alpha == undefined? 1 : alpha;
        if(/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)){
            c= hex.substring(1).split('');
            if(c.length== 3){
                c= [c[0], c[0], c[1], c[1], c[2], c[2]];
            }
            c = '0x'+c.join('');
            return 'rgba('+[(c>>16)&255, (c>>8)&255, c&255].join(',')+',' + alpha + ')';
        }
        return '';
    }

    RGBtoHSV(r, g, b) {
        if (arguments.length === 1) {
            g = r.g, b = r.b, r = r.r;
        }
        var max = Math.max(r, g, b), min = Math.min(r, g, b),
            d = max - min,
            h,
            s = (max === 0 ? 0 : d / max),
            v = max / 255;

        switch (max) {
            case min: h = 0; break;
            case r: h = (g - b) + d * (g < b ? 6: 0); h /= 6 * d; break;
            case g: h = (b - r) + d * 2; h /= 6 * d; break;
            case b: h = (r - g) + d * 4; h /= 6 * d; break;
        }

        return {
            h: h,
            s: s,
            v: v
        };
    }

    HSVtoRGB(h, s, v) {
        var r, g, b, i, f, p, q, t;
        if (arguments.length === 1) {
            s = h.s, v = h.v, h = h.h;
        }
        i = Math.floor(h * 6);
        f = h * 6 - i;
        p = v * (1 - s);
        q = v * (1 - f * s);
        t = v * (1 - (1 - f) * s);
        switch (i % 6) {
            case 0: r = v, g = t, b = p; break;
            case 1: r = q, g = v, b = p; break;
            case 2: r = p, g = v, b = t; break;
            case 3: r = p, g = q, b = v; break;
            case 4: r = t, g = p, b = v; break;
            case 5: r = v, g = p, b = q; break;
        }
        return {
            r: Math.round(r * 255),
            g: Math.round(g * 255),
            b: Math.round(b * 255)
        };
    }

    fromComponentsToHexString(r, g, b){
        var transitionColorR = this.pad(r.toString(16), "0", true, 2);
        var transitionColorG = this.pad(g.toString(16), "0", true, 2);
        var transitionColorB = this.pad(b.toString(16), "0", true, 2);
        return "#" + transitionColorR + transitionColorG + transitionColorB;	
    }

    colorTransition(startColor, endColor, weight){
        var f = parseInt(startColor.slice(1),16); 
        var startR = f >> 16; 
        var startG = f >> 8 & 0x00FF;
        var startB = f & 0x0000FF;
        f = parseInt(endColor.slice(1),16); 
        var endR = f >> 16; 
        var endB = f & 0x0000FF;
        var endG = f >> 8 & 0x00FF;
        return this.fromComponentsToHexString(Math.round(this.clamp((startR + (endR - startR) * weight),0,255)), 
            Math.round(this.clamp((startG + (endG - startG) * weight),0,255)),
            Math.round(this.clamp((startB + (endB - startB) * weight),0,255)));
    }

    componentToHex(c) {
        var hex = c.toString(16);
        return hex.length == 1 ? "0" + hex : hex;
    }

    rgbToHex(r, g, b) {
        return "#" + this.componentToHex(r) + this.componentToHex(g) + this.componentToHex(b);
    }

    hexToRgb(hex) {
        var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
        return result ? {
            r: parseInt(result[1], 16),
            g: parseInt(result[2], 16),
            b: parseInt(result[3], 16)
        } : null;
    }
}